// В этом файле должны быть стили для БЭМ-блока main, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.main {
	width: 100%;
	justify-content: center;
	flex-flow: column nowrap;
	align-items: center;

	&-wrap {

	}

	&__block {
		display: flex;
		align-items: center;
		flex-flow: column nowrap;
		margin-top: 40px;
		padding: 0 10px;

		&--border {
			border-top: 1px solid #DFDFDF;
			border-bottom: 1px solid #DFDFDF;
		}

		&:first-child {
			margin-top: 0;
		}

		&-wrap {
			display: flex;
			flex-flow: row wrap;
			justify-content: center;
			width: 100%;
		}

		&-title {
			border-bottom: 4px solid $green;

			span {
				line-height: 30px;
				text-transform: uppercase;
				font-variant: all-small-caps;
			}
		}

		&-option {
			display: flex;
			flex-flow: row wrap;
			max-width: 210px;
			width: 100%;
			border: 2px solid $green;
			position: relative;
			padding: 50px 25px 40px;
			margin-top: 50px;
			margin-right: 15px;

			&:last-child {
				margin-right: 0;
			}

			span {
				text-align: center;
			}
		}

		&--form {
			background-color: #F8F8F8;
			padding: 0 0 35px 0;
			border-bottom: 1px solid #DFDFDF;
			margin: 0;
			width: 100%;

			.main__block-wrap {
				display: flex;
				flex-flow: column nowrap;
				align-items: center;
			}

			.main__block-title {
				border: none;
			}
		}
	}
}

.option__img {
	position: absolute;
	left: 50%;
	top: 0;
	transform: translate(-50%, -50%);
	background-color: $white;
	overflow: hidden;
	padding: 0 15px;
}

.benefit {
	display: flex;
	flex-flow: column nowrap;
	max-height: 130px;
	max-width: 130px;
	height: 100%;
	width: 100%;
	align-items: center;
	text-align: center;
	margin-right: 65px;

	&:last-child {
		margin-right: 0;
	}

	&__img {
		height: 70px;
		display: flex;
		align-items: center;
	}

	&__name {
		margin-top: 15px;

		span {
			font-weight: bold;
		}
	}
}

.stage {
	display: flex;
	flex-flow: column nowrap;
	max-width: 140px;
	width: 100%;
	text-align: center;
	margin-right: 60px;

	&:last-child {
		margin-right: 0;
	}

	&__number {
		font-size: 64px;
		color: $green;
		font-weight: bold;

		span {

		}
	}

	&__text {
		margin-top: 15px;

		span {

		}
	}
}

.consultant {
	margin-top: 30px;

	&__description {
		margin-left: 25px;

		&-name {
			font-weight: bold;
			font-size: 18px;
		}

		span {
			padding-bottom: 20px;
		}
	}
}

.cost-consult {
	max-width: 500px;
	width: 100%;
	padding: 55px 0 80px;
	text-align: center;

	&__button {
		justify-content: space-between;
		margin-top: 30px;
	}

	span {
		font-size: 30px;
	}
}

@media (max-width: 768px) {

	.main {

		&__block {

			&-option {
				margin-right: 0;
				max-width: 270px;
				font-size: 23px;

				&:nth-child(2n - 1) {
					margin-right: 40px;
				}

				&:nth-child(3),
				&:nth-child(4) {
					margin-top: 70px;
				}
			}

			&--benefit {

				.main__block-wrap {
					justify-content: space-between;
				}
			}

			&--form {

				.main__block-wrap {
					padding: 0 40px;
				}
			}

			&--stage {

				.main__block-wrap {
					justify-content: space-between;
				}
			}
		}
	}

	.benefit {
		margin: 0;
	}

	.stage {
		margin: 0;
	}
}

@media (max-width: 600px) {

	.main {

		&__block {

			&-wrap {
				flex-flow: column nowrap;
				align-items: center;
			}

			&-option {
				margin-right: 0 !important;
				font-size: 16px;
				max-width: 210px;
			}

			&--benefit {

				.main__block-wrap {
					flex-flow: row wrap;
					justify-content: center;
				}
			}
		}
	}

	.benefit {
		margin-right: 20px;
		margin-top: 20px;
	}
}

@media (max-width: 480px) {

	.main {

		&__block {

			&--form {
				text-align: center;

				.main__block-wrap {
					padding: 0 10px;
				}
			}
		}
	}

	.consultant {
		flex-flow: column nowrap;

		img {
			width: 100%;
		}

		&__description {
			font-size: 18px;
			margin-top: 15px;

			span {
				position: relative;
			}

			span:not(.consultant__description-name)::before {
				content: '';
				display: block;
				position: absolute;
				width: 9px;
				height: 9px;
				background-color: $green;
				left: -15px;
				top: 7px;
			}
		}
	}

	.cost-consult {

		&__button {
			flex-flow: column nowrap;
			justify-content: center;
			align-items: center;

			a:first-child {
				margin-bottom: 20px;
			}
		}
	}

	.footer {
		text-align: center;
	}
}
