body {
  color: $black;
  height: 100%;
  width: 100%;
  font-family: 'ProximaNovaRegular', sans-serif;
}

.page {
	width: 100%;
	height: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
}

.container {
	max-width: 1024px;
	width: 100%;
}

.hidden {
  display: none;
}

.flex {
	display: flex;
}

.flex-column {
	display: flex;
	flex-flow: column nowrap;
}

.button {
	width: 100%;
	height: 50px;
	max-width: 233px;
	display: flex;
	justify-content: center;
	align-items: center;
	outline: none;
	border: none;
	cursor: pointer;
	background-color: $green;
	color: $white;
}

a {
	color: $green;
}

h1 {
	font-size: $font-size--h1;
	margin: 1.5em 0;
}

h2 {
	font-size: $font-size--h2;
	margin: 1.3em 0;
}

h3 {
	font-size: $font-size--h3;
	margin: 1em 0;
}

h4 {
	font-size: $font-size--h4;
	margin: 1em 0;
}

h5,
h6 {
	font-size: $font-size--h5;
	margin: 1em 0;
}
