// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

header {
	width: 100%;
	height: 365px;
	background: url('../img/header.png') center center no-repeat;
	background-size: cover;
	display: flex;
	align-items: center;

	h1 {
		max-width: 70%;
	}
}

.header-wrap {
	margin-left: 60px;
}

.header__button {
	font-size: 24px;
	text-transform: uppercase;
}

@media (max-width: 768px) {

	header {
		background: none;
		background-color: #F8F8F8;
		height: 250px;

		h1 {
			margin-top: 0;
		}
	}

	.header-wrap {
		margin-left: 0;
		justify-content: center;
		text-align: center;
		align-items: center;
	}
}
