// В этом файле должны быть стили для БЭМ-блока form, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.form {
	max-width: 750px;
	width: 100%;
	margin-bottom: 20px;

	textarea {
		max-width: 530px;
		width: 100%;
		border: 1px solid #DFDFDF;
		padding: 15px 0 0 20px;
		height: 170px;

		&:focus {
			outline: none;
		}
	}

	input {
		width: 200px;
		border: 1px solid #DFDFDF;
		height: 50px;
		font-size: 12px;
		padding-left: 10px;
	}

	&__wrap {
		margin-left: 20px;
		justify-content: space-between;

		button {
			text-transform: uppercase;
			max-width: 200px;
			width: 100%;
			padding: 0 15px;
		}
	}
}

@media (max-width: 480px) {

	.form {
		max-width: none;
		flex-flow: column nowrap;

		input,
		button {
			width: 100%;
			max-width: none;
		}

		textarea,
		input {
			margin-bottom: 15px;
		}

		&__wrap {
			margin: 0;
		}
	}
}
