// В этом файле должны быть стили для БЭМ-блока footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.footer {
	background-color: #F8F8F8;
	padding: 50px 10px;
	width: 100%;
	justify-content: center;

	&-wrap {
		align-items: center;
		text-align: center;
	}

	&__adress {
		margin: 20px 0;
	}

	a {
		color: inherit;
	}

	&__copyright {
		color: #CACACA;
	}
}

